"use client";
import { brands } from "@/data/brands";
import Image from "next/image";

export default function Brands() {
  // Dupliquer les logos pour un défilement continu
  const duplicatedBrands = [...brands, ...brands];

  return (
    <div className="brands-container overflow-hidden">
      <div className="brands-scroll">
        {duplicatedBrands.map((brand, index) => {
          // Réduire la taille du logo brand-06.svg de 20%
          const isBrand06 = brand.src.includes('brand-06.svg');
          const logoWidth = isBrand06 ? 132 : 165; // 165 * 0.8 = 132
          const logoHeight = isBrand06 ? 38 : 48;  // 48 * 0.8 = 38.4 ≈ 38
          
          return (
            <div
              className="brand-item d-inline-flex align-items-center justify-content-center"
              key={index}
              style={{ minWidth: '200px', marginRight: '40px' }}
            >
              <Image
                className="brand-item-image h-40px"
                src={brand.src}
                width={logoWidth}
                height={logoHeight}
                alt={brand.alt}
                style={isBrand06 ? { transform: 'scale(0.8)' } : {}}
              />
            </div>
          );
        })}
      </div>
      
      <style jsx>{`
        .brands-container {
          width: 100%;
          white-space: nowrap;
        }
        
        .brands-scroll {
          display: inline-flex;
          animation: scroll-left 30s linear infinite;
        }
        
        @keyframes scroll-left {
          0% {
            transform: translateX(0);
          }
          100% {
            transform: translateX(-50%);
          }
        }
        
        .brands-container:hover .brands-scroll {
          animation-play-state: paused;
        }
      `}</style>
    </div>
  );
}
